@import url('https://fonts.googleapis.com/css2?family=Cantata+One&family=Kanit:ital,wght@0,100;0,200;0,300;0,400;0,500;0,600;0,700;0,800;0,900;1,100;1,200;1,300;1,400;1,500;1,600;1,700;1,800;1,900&family=Libre+Baskerville:ital,wght@0,400;0,700;1,400&family=Lora:ital,wght@0,400..700;1,400..700&display=swap');
.skill{
    margin-bottom: 50px;
    top: 0;
}
.skill-list-name{
    text-align: center;
    font-family: "Cantata One", serif;
    font-weight: 400;
    font-style: normal;

}
.skill-heading{
    color: rgb(252, 32, 32);
    text-align: center;
    padding-top:20px;
    font-size: 29px;
    font-family: "Kanit", sans-serif;
    font-weight: 400;
    font-style: bold;
}
.skill-heading2{
    color:black;
    padding-top: 30px;
}
.skill-para{
    color:black;
    text-align: center;
    font-family: "Cantata One", serif;
    font-weight: 400;
    font-size: 19px;
    font-style: normal;
}
.skill-img{
    height: 400px;
    width: 300px;
    filter: brightness(100%) contrast(130%);
}
.skillset{
    padding-top: 50px;
}
.html, .css, .react, .bootstrap, .java, .github{
    height: 80px;
    width: 90px;
    margin: 20px;
    color:cadetblue; 
    border: 3px  solid black;
    border-radius: 20px;
    padding: 5px;
}
@media only screen and (max-width: 600px) {
    .skill-heading{
        text-align: center;
    }
    .skill-para{
        color:#000;
        font-size: 15px;
        margin: 20px;
        text-align: center;    
    }
    .skill-content{
        display: flex;
        flex-direction: column-reverse;
    }
    .skill-img{
        margin-left: 75px;
        height: 200px;
        margin-top: 20px;
        width: 120;
    }
    .skill-list{
        display: flex;
        flex-direction: column;
        align-items: center;
    }
}